import React from 'react';
import './DonutChart.css';
import { Doughnut } from 'react-chartjs-2';

class DonutChart extends React.Component {
    render() {
        return (
            <div className="dashboard__itemcontainer container__small">
                <div className="userchart__header">
                    <h4 className="dashboard__title">Departments</h4>
                </div>
                <div className="userchart__chartcontainer">
                    <Doughnut 
                            data={this.props.doughnutdata} 
                            options={{
                                cutoutPercentage: 85,
                                rotation: -1 * Math.PI,
                                circumference: Math.PI,
                                responsive: true,
                                maintainAspectRatio: false,
                                legend: {
                                    position: 'bottom'
                                },
                                layout: {
                                    padding: {
                                        left: 20,
                                        right: 20,
                                        top: 0,
                                        bottom: 0
                                    }
                                },
                                tooltips: {
                                    backgroundColor: '#01b075',
                                    displayColors: false,
                                    xPadding: 10
                                }
                    }}/>
                </div>
                <div>
                </div>
            </div>
        )
    }
}

export default DonutChart;