import React from 'react';
import { FaThLarge, FaUserFriends, FaBuilding } from 'react-icons/fa';
import './Menu.css';


class Menu extends React.Component {
    constructor(props) {
        super(props)

        this.showNotImplemented = this.showNotImplemented.bind(this);
    }

    showNotImplemented() {
        this.props.toggleNotification("This feature has not been implemented yet");
    }

    render() {
        return (
            <div className="menu__container">
                <h1 className="menu__logo">U</h1>
                <button className="menu__link menu__link-active"><FaThLarge className="menu__icon"/><span>Overview</span></button>
                <button className="menu__link" onClick={this.showNotImplemented}><FaUserFriends className="menu__icon"/><span>Users</span></button>
                <button className="menu__link" onClick={this.showNotImplemented}><FaBuilding className="menu__icon"/><span>Departments</span></button>
            </div>
        )
    }
};

export default Menu;