import React from 'react';
import './UserEntry.css';

class UserEntry extends React.Component {
    showUserStatus() {
        if(this.props.employee.isActive) {
            return <div className="user__status">Active</div>
        } else {
            return <div className="user__status-inactive">Inactive</div>
        }
    }

    render() {
        return (
            <div className="user__container">
                <div className="user__column column-small">
                    <div className="user__profile"><img src={ this.props.employee.image || "./img/profilepic-empty.png" } alt="Profile" className="user__picture" /></div>
                </div>
                <div className="user__column column-large">
                    <p className="user__name">{ this.props.employee.employeeName }</p>
                </div>
                <div className="user__column column-medium">
                    <p className="user__attribute">{ this.props.employee.jobTitle }</p>
                </div>
                <div className="user__column column-medium tablet-hide">
                    <p className="user__attribute">{ this.props.employee.birthDate }</p>
                </div>
                <div className="user__column column-large tablet-hide">
                    <p className="user__attribute">{ this.props.employee.email }</p>
                </div>
                <div className="user__column column-medium">
                    <p className="user__attribute">{ this.props.employee.phoneNumber }</p>
                </div>
                <div className="user__column column-medium tablet-hide">
                    <p className="user__attribute">{ this.props.employee.addressCity }</p>
                </div>
                <div className="user__column column-medium">
                    { this.showUserStatus() }
                </div>
            </div> 
        )
    }
};

export default UserEntry;