import React from 'react';
import Modal from '../Modal/Modal';
import Menu from '../Menu/Menu';
import Dashboard from '../Dashboard/Dashboard';
import employeeData from '../../tableData';
import NewUserForm from '../NewUserForm/NewUserForm';
import { FaTimes } from 'react-icons/fa';
import './App.css';

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = { 
      employees: employeeData, 
      showModal: false, 
      showNotification: false,
      notificationMessage: '',
      modalContent: '', 
      userChartData: {},
      DonutChartData: {},
  };

    this.getModalContent = this.getModalContent.bind(this);
    this.toggleModal = this.toggleModal.bind(this);
    this.toggleNotification = this.toggleNotification.bind(this);
    this.addUser = this.addUser.bind(this);
    this.filterTable = this.filterTable.bind(this);
    this.getJobTitlesForFilter = this.getJobTitlesForFilter.bind(this);
    this.getCitiesForFilter = this.getCitiesForFilter.bind(this);
    this.getUserChartLabels = this.getUserChartLabels.bind(this);
    this.getUserChartData = this.getUserChartData.bind(this);
    this.getUserTotals = this.getUserTotals.bind(this);
    this.getDepartmentChartData = this.getDepartmentChartData.bind(this);
  }

  componentDidMount() {
    this.getUserChartData();
    this.getDepartmentChartData();
  }

  toggleModal(content) {
    this.setState({
      modalContent: content,
      showModal: !this.state.showModal
   });
   document.getElementById("modal").focus();
  }

  toggleNotification(message) {
    this.setState({showNotification: true, notificationMessage: message});
    const timeout = setTimeout(()=> {
      this.setState({showNotification: false, notificationMessage: ''});
    }, 2000);
  }

  getModalContent() {
    switch(this.state.modalContent) {
      case 'new-user': 
        return <NewUserForm addUser={this.addUser} toggleModal={this.toggleModal}/>;
      default:  
        break;
    }
  }

  addUser(userData) {
    let id = employeeData.reduce((maxId, item) => Math.max(maxId, item.id), 0) + 1;
    userData.id = id;
    employeeData.push(userData);
  }
 
  filterTable(obj) {
    let userData = employeeData.filter(user => { 
              return (
                user.employeeName.toLowerCase().includes(obj.searchTerm.toLowerCase()) | 
                user.jobTitle.toLowerCase().includes(obj.searchTerm.toLowerCase()) | 
                user.email.toLowerCase().includes(obj.searchTerm.toLowerCase()) | 
                user.addressCity.toLowerCase().includes(obj.searchTerm.toLowerCase())
              )  
            });
    userData = userData.filter(user => user.jobTitle.includes(obj.jobTitle));
    userData = userData.filter(user => user.addressCity.includes(obj.city));
    if(obj.status !== "") {
      const isActive = obj.status === "true" ? true : false;
      userData = userData.filter(user => user.isActive === isActive);
    };
    this.setState({ employees: userData });
  }
  
  resetTable() {
    this.setState({ employees: employeeData });
  }

  getJobTitlesForFilter() {
    let counter = 0;
    const distinctJobtitles = [...new Set(employeeData.map(el => el.jobTitle))];
    const jobTitles = distinctJobtitles.map(el => {
        return {
          id: counter++,
          name: el
        }
    });
    return jobTitles
  }

  getCitiesForFilter() {
    let counter = 0;
    const distinctCities = [...new Set(employeeData.map(el => el.addressCity))];
    const cities = distinctCities.map(el => {
      return {
        id: counter++,
        name: el
      }
    });
    return cities
  }

  getUserTotals() {
    const totalsArray = [];
    totalsArray.push(employeeData.length);
    let totalUsersPercentage = Math.floor(((employeeData.length / 22 - 1) * 100) * 100) / 100;
    totalUsersPercentage = totalUsersPercentage >= 0 ? "+" + totalUsersPercentage : totalUsersPercentage;
    totalsArray.push(totalUsersPercentage);

    const newUsers = employeeData.length - 22;
    let newUserPercentage = Math.floor(((newUsers / 1) * 100) * 100) / 100;
    newUserPercentage = newUserPercentage >= 0 ? "+" + newUserPercentage : newUserPercentage;
    totalsArray.push(newUsers);
    totalsArray.push(newUserPercentage);

    const activeUsers = employeeData.filter(obj => obj.isActive === true);
    totalsArray.push(activeUsers.length)
    let activeUserPercentage =  Math.floor(((activeUsers.length / 12 - 1) * 100) * 100) / 100;
    activeUserPercentage = activeUserPercentage >= 0 ? "+" + activeUserPercentage : activeUserPercentage;
    totalsArray.push(activeUserPercentage);

    const getDepartmentBoardUsers = employeeData.filter(obj => obj.department === 'Board');
    totalsArray.push(getDepartmentBoardUsers.length);

    const getDepartmentFinanceUsers = employeeData.filter(obj => obj.department === 'Finance');
    totalsArray.push(getDepartmentFinanceUsers.length);

    const getDepartmentRDUsers = employeeData.filter(obj => obj.department === 'R&D');
    totalsArray.push(getDepartmentRDUsers.length);

    const getDepartmentSalesUsers = employeeData.filter(obj => obj.department === 'Sales');
    totalsArray.push(getDepartmentSalesUsers.length);

    return totalsArray;
  }

  getUserChartLabels() {
    const monthLabels = [];
     
      for(let i=5; i >= 0 ; i--) { 
        const date = new Date();
        const currentMonth = new Date(date.setMonth(date.getMonth() - i)).toLocaleString('default', { month: 'short' });
        monthLabels.push(currentMonth);
      }

    return monthLabels;
  }

  getUserChartData() {
    const chartData = {
      labels: this.getUserChartLabels(),
      datasets: [
          {
          label: '# of active users',
          data: [12, 10, 11, 9, 13],
          fill: false,
          backgroundColor: '#01b075',
          borderColor: '#c3e1e1',
          }
        ] 
      }
      const totalUsers = this.getUserTotals()[4];
      
    chartData.datasets[0].data.push(totalUsers);
    this.setState({ userChartData: chartData});
  }

  getDepartmentChartData() {
    const userTotals = this.getUserTotals();
    const state = {
      datasets: [
        {
          data: [],
          backgroundColor: ['#01b075', '#52c9a1', '#a3e2cd', '#c3e1e1']
        },
      ],
      labels: [
          'Board', 'R&D', 'Finance', 'Sales'
      ]}

    state.datasets[0].data.push(userTotals[6]);
    state.datasets[0].data.push(userTotals[7]);
    state.datasets[0].data.push(userTotals[8]);
    state.datasets[0].data.push(userTotals[9]);

    this.setState({DonutChartData: state});
  }

  render() {
    const modalContent = this.getModalContent();

    return (
      <div className="App">
        {
               this.state.showModal ? (
                  <Modal >
                      <div className="modal__header">
                        <h4 className="modal__title">Add an user</h4> 
                        <button 
                        className="modal__close"
                        onClick={this.toggleModal}>
                          <FaTimes />
                        </button>
                      </div>
                     {modalContent}
                  </Modal>
               ) : null
            }
            <div className={"notification__wrapper  " + (this.state.showNotification && "notification__open")}>
              <div className="notification__item">{this.state.notificationMessage}</div>
            </div>
        <Menu toggleNotification={this.toggleNotification}/>
        <Dashboard 
          userchartdata={this.state.userChartData}
          doughnutdata={this.state.DonutChartData}
          tickers={this.getUserTotals()}
          employees={this.state.employees} 
          jobtitles={this.getJobTitlesForFilter()} 
          cities={this.getCitiesForFilter()} 
          search={this.filterTable} 
          toggleModal={this.toggleModal} 
        />
      </div>
    );
  }
};

export default App;
