import React from 'react';
import './Tickers.css';
import { FaArrowAltCircleUp } from 'react-icons/fa';


class Tickers extends React.Component {
    constructor(props) {
        super(props);
        this.getSubheaderType = this.getSubheaderType.bind(this);
        this.getSubheaderIcon = this.getSubheaderIcon.bind(this);
        this.getSubheaderText = this.getSubheaderText.bind(this);
    }

    getSubheaderType(id) {
        const isNegative = this.props.tickers[id] <= 0 ? true : false;
        return isNegative;
    }
    
    getSubheaderIcon(id) {
        return this.getSubheaderType(id) ? "ticker__icon-down" : "ticker__icon-up";
    }

    getSubheaderText(id) {
        return this.getSubheaderType(id) ? "ticker__change-down" : "ticker__change-up";
    }

    getSub

    render() {
        return (
            <div className="dashboard__itemcontainer container__large">
                <div className="ticker__container">
                    <div className="ticker__item">
                        <h4 className="ticker__header">Total users</h4>
                        <p className="ticker__data">{this.props.tickers[0]}</p>
                        <p className={this.getSubheaderText(1)}><FaArrowAltCircleUp className={this.getSubheaderIcon(1)}/>{this.props.tickers[1]}%</p>
                    </div>
                    <div className="ticker__item">
                        <h4 className="ticker__header">New users</h4>
                        <p className="ticker__data">{this.props.tickers[2]}</p>
                        <p className="ticker__change-up"><FaArrowAltCircleUp className={this.getSubheaderIcon(3)}/>{this.props.tickers[3]}%</p>
                    </div>
                    <div className="ticker__item">
                        <h4 className="ticker__header">Avg. active per day</h4>
                        <p className="ticker__data">6,53 hr</p>
                        <p className="ticker__change-up"><FaArrowAltCircleUp className="ticker__icon-up"/>+1,4%</p>
                    </div>
                    <div className="ticker__item">
                        <h4 className="ticker__header">Pending applications</h4>
                        <p className="ticker__data">2</p>
                        <p className="ticker__change-down"><FaArrowAltCircleUp className="ticker__icon-down"/>-3,5%</p>
                    </div>
                </div>
            </div>
        )
    }
}

export default Tickers;