import React from 'react';
import { createPortal } from 'react-dom';
import './Modal.css';
const modalRoot = document.getElementById('modal');
const modalOverlay = document.getElementById('modal-overlay');
let modalClass = modalRoot.classList;
let modalOverlayClass = modalOverlay.classList;

class Modal extends React.Component {
    constructor(props) {
        super(props);
        this.element = document.createElement( 'div' )
        this.element.className = "modal__wrapper";
    }

    componentDidMount() {
        modalClass.add("modal-open");
        modalOverlayClass.add('modal-open');
        modalRoot.appendChild( this.element );
     }
    
     componentWillUnmount() {
        modalClass.remove("modal-open");
        modalOverlayClass.remove('modal-open');
        modalRoot.removeChild( this.element );
    }

    render() {
        return createPortal( this.props.children, this.element );
     }
}

export default Modal;