import React from 'react';
import './UserTable.css';
import UserEntry from '../UserEntry/UserEntry';
import { FaAngleDown, FaSearch, FaPlus } from 'react-icons/fa';

class UserTable extends React.Component {
    constructor(props) {
        super(props);
        this.state = { isShowFilters: false, filters: { searchTerm: "", status: '', jobTitle: "", city: "" } };
        this.toggleAddUserModal = this.toggleAddUserModal.bind(this);
        this.toggleUserTableFilters = this.toggleUserTableFilters.bind(this);
        this.handleSearchChange = this.handleSearchChange.bind(this);
        this.handleStatusChange = this.handleStatusChange.bind(this);
        this.handleCityChange = this.handleCityChange.bind(this);
        this.handleJobTitleChange = this.handleJobTitleChange.bind(this);
        this.filterTable = this.filterTable.bind(this);
    }

    toggleAddUserModal() {
        this.props.toggleModal('new-user');
    }

    toggleUserTableFilters() {
        const newState = !this.state.isShowFilters
        this.setState( { isShowFilters: newState });
    } 

    handleSearchChange(e) {
        let newFilters = this.state.filters;
        newFilters.searchTerm = e.target.value; 
        this.setState({ filters: newFilters });
        this.filterTable()
    }

    handleStatusChange(e) {
        let newFilters = this.state.filters;
        newFilters.status = e.target.value;
        this.setState({ filters: newFilters });
        this.filterTable();
    }

    handleCityChange(e) {
        let newFilters = this.state.filters;
        newFilters.city = e.target.value;
        this.filterTable();
    }

    handleJobTitleChange(e) {
        let newFilters = this.state.filters;
        newFilters.jobTitle = e.target.value;
        this.filterTable();
    }

    filterTable(e) {
        this.props.search(this.state.filters);
    }

    render() {
        return (
            <div className="usertable__container">
                <div className="usertable__header"> 
                    <h4 className="usertable__headertitle">Users</h4>
                    <div className="usertable__actions">
                        <button onClick={this.toggleAddUserModal} className="usertable__button"><FaPlus className="usertable__icon"/>Add</button>
                        <button onClick={this.toggleUserTableFilters} className="usertable_filterbutton">Filters <FaAngleDown className={ this.state.isShowFilters && "filter-open"} /></button>
                    </div>
                </div>
                <div className={ "usertable__filters " + (this.state.isShowFilters && "filterbar-open")} >
                    <div className="usertable__switch">
                        <input type="radio" id="inp-status-all" name="status" value="" onChange={this.handleStatusChange} className="usertable__buttongroupinput" />
                        <label htmlFor="inp-status-all" className="usertable__buttongroupitem">All</label>
                        <input type="radio" id="inp-status-active" name="status" value="true" onChange={this.handleStatusChange} className="usertable__buttongroupinput" />
                        <label htmlFor="inp-status-active" className="usertable__buttongroupitem">Active</label>
                        <input type="radio" id="inp-status-inactive" name="status" value="false" onChange={this.handleStatusChange} className="usertable__buttongroupinput" />   
                        <label htmlFor="inp-status-inactive" className="usertable__buttongroupitem">Inactive</label>                     
                    </div>
                    <div>
                        <select name="jobtitle" id="inp-jobtitle" onChange={this.handleJobTitleChange} className="usertable__selectfilter">
                            <option value="" className="usertable__selectoption">- Select a job title -</option> 
                            {this.props.jobtitles.map(el => <option key={el.id} value={el.name} className="usertable__selectoption">{el.name}</option> )}
                        </select>
                    </div>
                    <div>
                        <select name="city" id="inp-city" onChange={this.handleCityChange} className="usertable__selectfilter">
                            <option value="" className="usertable__selectoption">- Select a city -</option> 
                            {this.props.cities.map(el => <option key={el.id} value={el.name} className="usertable__selectoption">{el.name}</option> )}
                        </select>
                    </div>
                    <div className="usertable__search"><input type="text" id="user-search" onChange={ this.handleSearchChange } className="usertable__searchinput" /><FaSearch className="usertable__searchicon" /></div>
                </div>
                {this.props.employees.map(employee => <UserEntry key={employee.id} employee={employee} />)}
            </div>
        )
    }
};

export default UserTable;