import React from 'react';
import './UsersChart.css';
import { FaArrowAltCircleUp } from 'react-icons/fa';
import { Line } from 'react-chartjs-2';

class UsersChart extends React.Component {
    static defaultProps = {
        displayTitle:true,
        displayLegend: false,
        legendPosition:'right',
        location:'City'
      }

    render() {
        return (
            <div className="dashboard__itemcontainer container__medium">
                <div className="userchart__header">
                    <h4 className="dashboard__title">Active users</h4>
                    <p className="userchart__subheader"><FaArrowAltCircleUp className="userchart__icon" /><span className="userchart__text-highlight">{this.props.userTotals[5]}%</span> this month</p>
                </div>
                <div className="userchart__chartcontainer">
                    <Line
                        data={this.props.userchartdata}
                        options={{
                            responsive: true,
                            maintainAspectRatio: false,
                            title:{
                            display:this.props.displayTitle
                            },
                            legend:{
                            display:this.props.displayLegend,
                            position:this.props.legendPosition
                            },
                            scales: {
                                xAxes: [{
                                    gridLines: {
                                        display:false
                                    }
                                }],
                                yAxes: [{
                                    gridLines: {
                                        display: true
                                    },
                                    ticks: {
                                        beginAtZero: true
                                    }   
                                }]
                            },
                            tooltips: {
                                backgroundColor: '#01b075',
                                displayColors: false,
                                xPadding: 10
                            }                        
                        }}
                    />
                </div>
            </div> 
        )
    }
}

export default UsersChart;